<template>
	<view style="background-color: #f1f1f1;height: 100vh;">
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">活动列表</block>
		</cu-custom>
		<!-- <view class="cu-bar search bg-white">
			<view class="search-form round">
				<text class="cuIcon-search"></text>
				<input :adjust-position="false" type="text" placeholder="搜索关键字" confirm-type="search"
					v-model="keyword"></input>
			</view>
			<view class="action">
				<button class="cu-btn bg-blue shadow-blur round" @tap="getshopData">搜索</button>
			</view>
		</view> -->






		<view class="text-xsl padding classicon"  style="background-color: #f1f1f1;" v-if="arr.length == 0">
			<text class=" cuIcon-info text-blue"></text>
			<view class="textclass">没有可查看的数据</view>
		</view>
		<view v-for="(item,index) in arr" :key="index">
			<view class="cu-card article" :class="isCard?'no-card':''" @click="clicfun(item)">
				<view class="cu-item shadow">
					<!-- 					<view class="title"><view class="text-cut">{{item.title}}</view></view> -->
					<view class="content" style="padding-top: 10px;">
						<image :src="item.img" mode="aspectFill"></image>
						<view class="desc" style="justify-content: space-around;">
							<view class="text-content" style="color: #000;font-size: 1.2em;font-weight: bold;">
								{{item.projectname}}
							</view>
							<view>
								<view class="cu-tag bg-green light sm round">详情：{{item.detail  | ellipsis }}</view>
							</view>
							<!-- <view>
								<view class="cu-tag bg-red light sm round">地址：{{item.address}}</view>
							</view> -->
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import configData from '@/config/index.js'
	const urllocal = configData.httpUrl
	export default {
		created() {

		},
		onShow() {
			this.getshopData()
		},
		onPullDownRefresh() {
			this.getshopData()
			uni.stopPullDownRefresh();
		},
		data() {
			return {
				keyword: '',
				type: '',
				arr: [],
				CustomBar: this.CustomBar,
			};
		},
		methods: {
			async getshopData() {
				let that = this
				let msgobj = {
					status: "已审核"
				}
				let resdata = await new this.Request(this.Urls.m().queryactivitystatus, msgobj).modepost()
				console.log(resdata)
				that.arr = resdata.map((val, i) => {
					val.img =  val.img.split('^^^')[0]
					return val
				})
			},
			clicfun(val) {
				console.log("123")
				uni.navigateTo({
					url: `/pages/user/activitydetail?id=${val.id}`,
					fail: (res) => {
						console.log(res) //打印错误信息
					}
				});
			},
			toshoptype(val) {
				uni.navigateTo({
					url: `/pages/indextype/indextype?typeid=${val.id}&typedesc=${val.text}`,
					fail: (res) => {
						console.log(res) //打印错误信息
					}
				});
			},
			IsCard(e) {
				this.isCard = e.detail.value
			},
			
			DotStyle(e) {
				this.dotStyle = e.detail.value
			},
			// cardSwiper
			cardSwiper(e) {
				this.cardCur = e.detail.current
			},
		},
		filters: {
			// 判断超出5个字显示省略号
			ellipsis(value) {
				if (!value) return ''
				if (value.length > 10) {
					return value.slice(0, 10) + '...'
				}
				return value
			},
		},
	}
</script>

<style scoped>
	.listtab {
		width: 100%;
		padding-left: 5%;
		padding-right: 5%;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.listtabsub {
		width: 18%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}



	.text-content {
		line-height: 1;
	}

	.cu-card.article>.cu-item .content .text-content {
		height: 1em;
	}

	.cu-card.article>.cu-item .content .text-content.data-v-71bf3ec6 {
		height: 1.3em;
	}

	.padding-sm {
		padding: 3rpx;
	}

	.bg-grey {
		background-color: #fff;
		color: #b1a1a1;
	}

	.margin-xs {
		margin: 3rpx;
	}

	.tabsub {
		width: 33%;
		font-size: 0.8em;
		color: #b1a1a1;
		font-weight: 800;
	}

	.textclass {
		margin-top: 5px;
		font-weight: 600;
		font-size: 15px;
		color: darkgray;
	}

	.classicon {
		height: 70vh;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		background-color: #fff;
		font-size: 10em;
	}
</style>
